.trans-mask {
  height: 100%;
  line-height: 40;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.trans-mask .trans-1 {
  text-align: center;
  margin: 20px 0;
}
.trans-mask .trans-1 .ball {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-block;
  -webkit-animation: trans1 1.4s infinite ease-in-out;
          animation: trans1 1.4s infinite ease-in-out;
}
.trans-mask .trans-1 .ball:nth-child(1) {
  background: #33afaa;
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}
.trans-mask .trans-1 .ball:nth-child(2) {
  background: #1b908b;
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}
.trans-mask .trans-1 .ball:nth-child(3) {
  background: #0c6b67;
}
@-webkit-keyframes trans1 {
0%, 80%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
}
40% {
    transform: scale(1);
    -webkit-transform: scale(1);
}
}
@keyframes trans1 {
0%, 80%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
}
40% {
    transform: scale(1);
    -webkit-transform: scale(1);
}
}
